'use client';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Badge } from '@/components/ui/Badge';
import { mockEvaluationForm } from '@/data/mock';

export default function EvaluationForms() {
  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Evaluation Forms</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          Create and manage evaluation forms with CHED compliance
        </p>
      </div>

      {/* Existing Forms */}
      <div className="space-y-4">
        <div className="flex justify-between items-center">
          <h2 className="text-xl font-bold text-gray-900 dark:text-white">Active Forms</h2>
          <Button variant="primary">Create New Form</Button>
        </div>

        <Card>
          <CardHeader>
            <div className="flex justify-between items-start">
              <div>
                <CardTitle>{mockEvaluationForm.name}</CardTitle>
                <CardDescription>{mockEvaluationForm.description}</CardDescription>
              </div>
              <div className="flex gap-2">
                <Badge variant="success">Published</Badge>
                <Badge variant="outline">CHED Compliant</Badge>
              </div>
            </div>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-3">
              {mockEvaluationForm.criteria.map((criteria) => (
                <div key={criteria.id} className="flex justify-between p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                  <div>
                    <p className="font-semibold text-gray-900 dark:text-white">{criteria.name}</p>
                    <p className="text-sm text-gray-600 dark:text-gray-400">{criteria.description}</p>
                  </div>
                  <div className="text-right">
                    <p className="font-semibold text-gray-900 dark:text-white">{criteria.weight}%</p>
                    <p className="text-sm text-gray-600 dark:text-gray-400">Max: {criteria.maxScore}</p>
                  </div>
                </div>
              ))}
            </div>

            <div className="flex gap-2 pt-4 border-t border-gray-200 dark:border-gray-700">
              <Button variant="outline">Edit</Button>
              <Button variant="outline">Duplicate</Button>
              <Button variant="danger">Archive</Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
